.uk-close::-moz-focus-inner {
    border: 0;
    padding: 0
}

.uk-close {
    -webkit-appearance: none;
    margin: 0;
    border: none;
    overflow: visible;
    font: inherit;
    color: inherit;
    text-transform: none;
    padding: 0;
    background: 0 0;
    display: inline-block;
    box-sizing: content-box;
    width: 20px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    opacity: .3
}

.uk-close:after {
    display: block;
    content: "\f00d";
    font-family: FontAwesome
}

.uk-close:focus, .uk-close:hover {
    opacity: .5;
    outline: 0;
    color: inherit;
    text-decoration: none;
    cursor: pointer
}

.uk-close-alt {
    padding: 2px;
    border-radius: 50%;
    background: #fff;
    opacity: 1;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .1), 0 0 6px rgba(0, 0, 0, .3)
}

.uk-close-alt:focus, .uk-close-alt:hover {
    opacity: 1
}

.uk-close-alt:after {
    opacity: .5
}

.uk-close-alt:focus:after, .uk-close-alt:hover:after {
    opacity: .8
}

.uk-badge {
    display: inline-block;
    padding: 0 5px;
    background: #00a8e6;
    font-size: 10px;
    font-weight: 700;
    line-height: 14px;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    text-transform: none;
    border: 1px solid rgba(0, 0, 0, .06);
    border-radius: 2px;
    text-shadow: 0 1px 0 rgba(0, 0, 0, .1)
}

a.uk-badge:hover {
    color: #fff
}

.uk-badge-notification {
    box-sizing: border-box;
    min-width: 18px;
    border-radius: 500px;
    font-size: 12px;
    line-height: 18px
}

.uk-badge-success {
    background-color: #8cc14c
}

.uk-badge-warning {
    background-color: #faa732
}

.uk-badge-danger {
    background-color: #da314b
}

.uk-alert {
    margin-bottom: 15px;
    padding: 10px;
    background: #ebf7fd;
    color: #2d7091;
    border: 1px solid rgba(45, 112, 145, .3);
    border-radius: 4px;
    text-shadow: 0 1px 0 #fff
}

* + .uk-alert {
    margin-top: 15px
}

.uk-alert > :last-child {
    margin-bottom: 0
}

.uk-alert h1, .uk-alert h2, .uk-alert h3, .uk-alert h4, .uk-alert h5, .uk-alert h6 {
    color: inherit
}

.uk-alert > .uk-close:first-child {
    float: right
}

.uk-alert > .uk-close:first-child + * {
    margin-top: 0
}

.uk-alert-success {
    background: #f2fae3;
    color: #659f13;
    border-color: rgba(101, 159, 19, .3)
}

.uk-alert-warning {
    background: #fffceb;
    color: #e28327;
    border-color: rgba(226, 131, 39, .3)
}

.uk-alert-danger {
